
import AlertModule from 'vux/src/plugins/alert/module'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import {mapMutations} from 'vuex'
import {login} from '../../util/beApi'

export default {
components: {
XInput,
XButton,
Group,
Cell,
XHeader,
Alert
},
data() {
return {
password: null,
username: null,
code: null,
}
},
methods: {
...mapMutations([
'RECORD_USERINFO',
]),
async loginA() {
let res = await login({
username: this.username.replace(/\s/g, ""),
password: this.password,
code: this.code
});
if (res.data.code == 1) {
this.RECORD_USERINFO({username: res.data.data.username});
let params = this.$route.params;
params && this.$router.push((params && params.redirect) ? params.redirect : '/home');
} else {
AlertModule.show({
title: '错误',
content: res.data.msg,
});
setTimeout(() => {
AlertModule.hide()
}, 3000)
}
},
}
}



