
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Drawer from 'vux/src/components/drawer/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import Vue from 'vue'
import {mapState, mapActions} from 'vuex'
import {logout} from '../../util/beApi'

Vue.use(ConfirmPlugin);

export default {
directives: {
TransferDom
},
components: {
CheckIcon,
Flexbox,
XButton,
FlexboxItem,
Radio,
Group,
Cell,
Badge,
Drawer,
ButtonTab,
ButtonTabItem,
ViewBox,
XHeader,
Tabbar,
TabbarItem,
Loading,
Actionsheet
},
methods: {
onClickMore() {
this.showMenu = true;
},
async clickMenu(menuKey, item) {
if (item && item.label === '注销登陆') {
let res = await logout();
if (res.data.code === 1) {
this.LOGOUT();
this.$router.push('/');
}
}
},
afterEnter() {
this.$vux && this.$vux.bus && this.$vux.bus.$emit('vux:after-view-enter');
},
...mapActions({LOGOUT: state => state.LOGOUT}),
delPics() {
let self = this;
this.$vux.confirm.show({
title: '删除图片',
content: '确定要全部删除？',
confirmText: '确定',
cancelText: '取消',
onConfirm() {
self.piclist.del = true;
}
})
}
},
computed: {
...mapState({
route: state => state.route,
path: state => state.route.path,
deviceready: state => state.app.deviceready,
demoTop: state => state.vux.demoScrollTop,
isLoading: state => state.vux.isLoading,
direction: state => state.vux.direction,
piclist: state => state.piclist
}),
leftOptions() {
return {
showBack: this.path != '/home'
}
},
rightOptions() {
return {
showMore: true
}
},
headerTransition() {
if (!this.direction) return ''
return this.direction === 'forward' ? 'vux-header-fade-in-right' : 'vux-header-fade-in-left'
},
title() {
return this.route.meta.title;
},
viewTransition() {
if (!this.direction) return ''
return 'vux-pop-' + (this.direction === 'forward' ? 'in' : 'out')
}
},
data() {
return {
showMenu: false,
showMode: 'push',
placement:
'left',
menus:
{
menu1: {
label: '注销登陆',
type:
'warn'
}
}
,
drawerVisibility: false,
}
}
}



