
import Scroller from 'vux/src/components/scroller/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import {getImgs} from "../../util/beApi";

export default {
components: {Scroller, XHeader, FlexboxItem, Flexbox, Previewer, XImg, TransferDom},
name: "anoymous",
directives: {
TransferDom
},
data() {
return {
pics: [],
index: 0,
options: {
getThumbBoundsFn(index) {
let thumbnail = document.querySelectorAll('.pics')[index]
let pageYScroll = window.pageYOffset || document.documentElement.scrollTop
let rect = thumbnail.getBoundingClientRect()
return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}
}
},
}
},
mounted() {
this.getPics();
},
methods: {
show(index) {
this.$refs.previewer.show(index);
},
loadMore() {
if (!this.onFetching) {
this.onFetching = true
setTimeout(() => {
this.index++;
this.getPics();
this.$nextTick(() => {
this.$refs.picScroll.reset()
});
this.onFetching = false
}, 1000)
}
},
async getPics() {
let res = await getImgs(15, this.index);
this.pics = this.pics.concat(res.data.data);
},
success() {

},
error() {

}
}
}



