import {uploadPic} from '../../util/beApi'

export default {
  data() {
    return {
      files: []
    }
  },
  methods: {
    reupload(index) {
      let pic = this.files[index];
      if (pic)
        uploadPic(pic.file, (e) => {
          if (e.lengthComputable) {
            pic.status = 'process';
            pic.process = Math.ceil(e.loaded / e.total);
          }
        }).then(() => {
          pic.status = 'success';
          pic.process = 0;
        }).catch(() => {
          pic.status = 'error';
          pic.process = 0;
        });
    },
    autoUpload(e) {
      let pics = e.target.files;
      let i = 0;
      let pic;
      let files = this.files;
      const reader = new FileReader();
      reader.readAsDataURL(pics[i]);
      reader.onload = function () {
        pic = {
          url: reader.result,
          status: 'unstart',
          process: 0,
          file: pics[i]
        };
        files.push(pic);
        uploadPic(pics[i], (e) => {
          if (e.lengthComputable) {
            pic.status = 'process';
            pic.process = Math.ceil(e.loaded / e.total);
          }
        }).then(() => {
          if (++i < pics.length) {
            reader.readAsDataURL(pics[i]);
          }
          pic.status = 'success';
          pic.process = 0;
        }).catch(() => {
          if (++i < pics.length) {
            reader.readAsDataURL(pics[i]);
          }
          pic.status = 'error';
          pic.process = 0;
        });
      }
    }
  }
}



